<template>
    <div>
        <TopNav mdl="agent" select="0"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p> 1）这里设置分销代理设置</p>
        </div>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <div class="wyu-title">
                    <h2>分销代理设置</h2>
                </div>
                <el-form :model="setting" label-width="150px" style="padding:20px;">
                    <h2 class="form-tit">普通顾问</h2>
                    <el-form-item label="升级条件">
                        推广顾问: <el-input size="small" v-model="setting.ordinary_num" style="width:100px"></el-input>
                        订单: <el-input size="small" v-model="setting.ordinary_order"  style="width:100px"></el-input>
                        注册: <el-input size="small" v-model="setting.ordinary_register"  style="width:100px"></el-input>
                        VIP: <el-input size="small" v-model="setting.ordinary_vip"  style="width:100px"></el-input>
                        <p>PS:升级条件是直接推广的成果（不包含下级顾问的推广成果）</p>
                    </el-form-item>
                    <el-form-item label="开通会员卡提成">
                        <el-input size="small" v-model="setting.ordinary_plus"  style="width:100px"></el-input>元；
                        <p>PS:直接推广开通的提成</p>
                    </el-form-item>
                    <el-form-item label="订单金额提成率">
                        <el-input size="small" v-model="setting.ordinary_rate"  style="width:100px"></el-input>%
                        ps:直接成交的订单提成率 保留两位小数，百分之一
                    </el-form-item>
                    <el-form-item label="子顾问会员卡提成">
                        <el-input size="small" v-model="setting.ordinary_child_plus"  style="width:100px"></el-input>元；
                        <p>PS:子顾问推广的会员卡提成</p>
                    </el-form-item>
                    <el-form-item label="子顾问订单提成率">
                        <el-input size="small" v-model="setting.ordinary_child_rate"  style="width:100px"></el-input>%
                        ps:子顾问成交的订单提成率保留两位小数 ，百分之一
                    </el-form-item>

                    <h2 class="form-tit">资深顾问</h2>
                    <el-form-item label="升级条件">
                        推广顾问: <el-input size="small" v-model="setting.senior_num" style="width:100px"></el-input>
                        订单: <el-input size="small" v-model="setting.senior_order"  style="width:100px"></el-input>
                        注册: <el-input size="small" v-model="setting.senior_register"  style="width:100px"></el-input>
                        VIP: <el-input size="small" v-model="setting.senior_vip"  style="width:100px"></el-input>
                        <p>PS:升级条件是直接推广的成果（不包含下级顾问的推广成果）</p>
                    </el-form-item>
                    <el-form-item label="开通会员卡提成">
                        <el-input size="small" v-model="setting.senior_plus"  style="width:100px"></el-input>元；
                        <p>PS:直接推广开通的提层</p>
                    </el-form-item>
                    <el-form-item label="订单金额提成率">
                        <el-input size="small" v-model="setting.senior_rate"  style="width:100px"></el-input>%
                        ps:直接成交的订单提成率 保留两位小数  百分之一
                    </el-form-item>
                    <el-form-item label="子顾问会员卡提成">
                        <el-input size="small" v-model="setting.senior_child_plus"  style="width:100px"></el-input>元；
                        <p>PS:子顾问推广的会员卡提成</p>
                    </el-form-item>
                    <el-form-item label="子顾问订单提成率">
                        <el-input size="small" v-model="setting.senior_child_rate"  style="width:100px"></el-input>%
                        ps:子顾问成交的订单提成率保留两位小数  百分之一
                    </el-form-item>


                    <h2 class="form-tit">专家顾问</h2>
                    <el-form-item label="升级条件">
                        推广顾问: <el-input size="small" v-model="setting.expert_num" style="width:100px"></el-input>
                        订单: <el-input size="small" v-model="setting.expert_order"  style="width:100px"></el-input>
                        注册: <el-input size="small" v-model="setting.expert_register"  style="width:100px"></el-input>
                        VIP: <el-input size="small" v-model="setting.expert_vip"  style="width:100px"></el-input>
                        <p>PS:升级条件是直接推广的成果（不包含下级顾问的推广成果）</p>
                    </el-form-item>
                    <el-form-item label="开通会员卡提成">
                        <el-input size="small" v-model="setting.expert_plus"  style="width:100px"></el-input>元；
                        <p>PS:直接推广开通的提成</p>
                    </el-form-item>
                    <el-form-item label="订单金额提成率">
                        <el-input size="small" v-model="setting.expert_rate"  style="width:100px"></el-input>%
                        ps:直接成交的订单提成率 保留两位小数  百分之一
                    </el-form-item>
                    <el-form-item label="子顾问会员卡提成">
                        <el-input size="small" v-model="setting.expert_child_plus"  style="width:100px"></el-input>元；
                        <p>PS:子顾问推广的会员卡提成</p>
                    </el-form-item>
                    <el-form-item label="子顾问订单提成率">
                        <el-input size="small" v-model="setting.expert_child_rate"  style="width:100px"></el-input>%
                        ps:子顾问成交的订单提成率保留两位小数  百分之一
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="saveForm()">立即保存</el-button>
            <el-button size="small">取消</el-button>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";


    export default {
        name: 'adminRole',
        components: {TopNav},
        data() {
            return {
                loading: false,
                setting: {
                    'ordinary_num' : 0, //推广的顾问
                    'ordinary_register': 0,//推广注册用户
                    'ordinary_order' : 0, //推广的订单数
                    'ordinary_vip' : 0, //推广的VIP数量
                    'ordinary_rate'  : 0, //百分之几
                    'ordinary_plus' : 0, //开PLUS会员提成
                    'ordinary_child_rate'  : 0, //百分之几
                    'ordinary_child_plus' : 0, //所有子顾问推广的PLUS会员卡提成

                    'senior_num'        : 0,
                    'senior_register'  : 0,
                    'senior_order'      : 0,
                    'senior_vip'        : 0,
                    'senior_rate'       : 0,//百分之几
                    'senior_plus'    : 0,//开通PLUS会员提成
                    'senior_child_rate' : 0,//所有子顾问推广的提成
                    'senior_child_plus' : 0, //所有子顾问推广的PLUS会员卡提成

                    'expert_num'       : 0,
                    'expert_register'   : 0,
                    'expert_order'      : 0,
                    'expert_vip'        : 0,
                    'expert_rate'       : 0,//百分知己
                    'expert_plus'    : 0,//开通PLUS会员提成
                    'expert_child_rate' : 0,//所有子顾问推广的提成
                    'expert_child_plus' : 0, //所有子顾问推广的PLUS会员
                }
            }
        },

        created() {
            this.getForm();
        },

        methods: {
            getForm(){
                this.loading = true;
                api.post("/shop/admin.agent.setting/detail", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.setting = res.detail;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            saveForm() {
                this.loading = true;
                api.post("/shop/admin.agent.setting/save", {
                    appToken: this.$store.getters.appToken,
                    datas: this.setting,
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            }
        }
    }
</script>
